import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhEcommerce, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.scss']
})
export class OrderComponent implements OnInit {
  searchValue: any;
  salesOrder: Array<any> = []
  salesSubOrder: Array<any> = []
  list_status_bill: Array<any> = ["All", "Paid", "Unpaid", "Lost"]
  heightScroll: any;
  widthScroll: any;
  public date: Date[] = [new Date(), new Date()];
  load_status: boolean = false;
  indexSegment: number = 0;
  /**Giới hạn thời gian cho xem bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < this.minDate.getTime() - 24 * 60 * 60 * 1000) return true
    return false
  }
  minDate: Date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 30)
  infoShop: any = {}
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhEcommerce: VhEcommerce,
    private cdRef: ChangeDetectorRef,
    private datePipe: DatePipe,
    private vhQuerySales: VhQuerySales
  ) {
  }

  ngOnInit(): void {
    this.infoShop = this.vhEcommerce.getlocalShopListShopee()[0];
    this.changeShop(this.infoShop._id)
    this.getMinDate()
  }
  /** 
   * Lấy ngày giới hạn được xem báo cáo
  */
  getMinDate() {
    let date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 61)
    this.vhQuerySales.getValidityDate(date).then((validitydate: Date) => {
      this.minDate = validitydate
    })
  }
  /**Tìm kiếm hóa đơn */
  search(value: string) {
    this.searchValue = value;
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.salesOrder = this.vhAlgorithm.searchList(val, this.salesSubOrder, ['code'])
    } else this.salesOrder = [...this.salesSubOrder]
  }
  /**Thay đổi segment tất cả, chờ xác nhận... */
  segmentChanged(event) {
    this.indexSegment = event
    switch (event) {
      case 0: this.salesOrder = this.salesSubOrder; break;
      case 1: this.salesOrder = this.salesSubOrder.filter(item => item.verify_status_payment); break; // .order_status == 8 : đã hoàn tất  và đã đối soát
      case 2: this.salesOrder = this.salesSubOrder.filter(item => !item.verify_status_payment); break;// .order_status == 8 : đã hoàn tất  và chưa đối soát
      case 3: this.salesOrder = this.salesSubOrder.filter(item => !item.hasOwnProperty('verify_status_received_goods') && item.status == 'canceled'); break;// .order_status == 13 : đã hủy và chưa đối soát
    }
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/report'])
  }
  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 1209600000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast")
      this.date = [event[0], event[0].getTime() + 1209600000]
    }
  }
  showReport() {
    this.load_status = true;
    this.salesOrder = []
    this.salesSubOrder = []
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhEcommerce.getSalesOrderListShopee(new Date(this.datePipe.transform(new Date(this.date[0].setHours(0, 0, 0)), "yyyy-MM-dd HH:mm:ss").toString()), new Date(this.datePipe.transform(new Date(this.date[1].setHours(23, 59, 59)), "yyyy-MM-dd HH:mm:ss").toString()), this.infoShop.access_token.toString(), this.infoShop._id.toString())
        .then((result: any) => {
          this.vhComponent.hideLoading(0);
          

          result.forEach(item => {
            if (item.status == 'TO_RETURN' && item.items[0].confirmation.status == 'ready_to_pick')
              this.salesOrder.push({
                ...item,
                id_shop: this.infoShop._id,
              })
            if (item.status == 'COMPLETED') this.salesOrder.push({
              ...item,
              id_shop: this.infoShop._id,
            })

          })
          this.salesSubOrder = this.salesOrder;
          this.segmentChanged(this.indexSegment);

        }).catch(err => this.vhComponent.hideLoading(0))
    })
  }
  changeShop(event) {
    this.salesOrder = [];
    this.salesSubOrder = [];
  }
  renderStatus(order_status, payment_status?) {
    if (order_status == 8 && payment_status == 4) { return "Paid" }
    else if (order_status == 8 && payment_status == 1) { return "Unpaid" }
    else { return "Cancelled" }
  }
  ngAfterViewChecked() {
    if (document.querySelector("#order-status-sendo") && document.querySelector(".ant-tabs-tab") && document.querySelector(".ant-table-thead") && document.querySelector(".order-status-sendo-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#order-status-sendo").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".order-status-sendo-header").clientHeight - document.querySelector(".ant-tabs-tab").clientHeight - 90 + "px";
    }
    if (document.getElementById("order-status-sendo"))
      this.widthScroll = document.getElementById("order-status-sendo").clientWidth + "px"
    this.cdRef.detectChanges();
  }
}
